<template>
  <div id="signup">
    <div class="signup-wrap">
      <div class="outter">
        <div class="inner">
          <div class="signup-container">
            <div class="signup-title-wrap">
              <h1 class="signup-title"></h1>
            </div>
            <div class="signup-box-wrap">
              <div class="signup-box uname">
                <div class="box-title-wrap">
                  <div class="outter">
                    <div class="inner">
                      <h2 class="box-title">
                        NAME
                      </h2>
                    </div>
                  </div>
                </div>
                <input
                  type="text"
                  v-model="user.uname"
                  class="signup-input"
                  title=" "
                  autocomplete="off"
                  placeholder=" "
                />
              </div>
              <div class="signup-box id">
                <div class="box-title-wrap">
                  <div class="outter">
                    <div class="inner">
                      <h2 class="box-title">
                        ID
                      </h2>
                    </div>
                  </div>
                </div>
                <input
                  type="text"
                  v-model="user.id"
                  class="signup-input"
                  title="ID "
                  autocomplete="off"
                  placeholder="ID "
                />
              </div>
              <div class="signup-box pw">
                <div class="box-title-wrap">
                  <div class="outter">
                    <div class="inner">
                      <h2 class="box-title">PW</h2>
                    </div>
                  </div>
                </div>
                <input
                  type="password"
                  v-model="user.pw"
                  class="signup-input"
                  title="PW "
                  autocomplete="off"
                  placeholder="PW "
                />
              </div>
              <div class="signup-box pw-confirm">
                <div class="box-title-wrap">
                  <div class="outter">
                    <div class="inner">
                      <h2 class="box-title">PW</h2>
                    </div>
                  </div>
                </div>
                <input
                  type="password"
                  v-model="user.pwConfirm"
                  class="signup-input"
                  title="PW  "
                  autocomplete="off"
                  placeholder="PW  "
                />
              </div>
              <div class="signup-box button">
                <button class="signup-button" v-on:click="onSubmit">
                  
                </button>
              </div>
              <div class="signup-login-wrap">
                <div class="signup-login-container">
                  <a
                    href="javascript:void(0)"
                    @click="goLogin"
                    class="login-link"
                    >  ?</a
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "login",
  components: {},
  data() {
    return {
      user: {
        uname: "",
        id: "",
        pw: "",
        pwConfirm: ""
      }
    };
  },
  computed: {
    ...mapGetters(["getUser"])
  },
  methods: {
    ...mapActions(["signup"]),
    ...mapActions(["setNotice"]),
    async onSubmit() {
      if (this.user.pw != this.user.pwConfirm) {
        this.setNotice({
          state: true,
          title: " ",
          body: "PW   ",
          button: "",
          style: {
            height: "100%",
            display: "inline-block"
          }
        });
        return;
      }
      await this.signup({
        uname: this.user.uname,
        uid: this.user.id,
        pw: this.user.pw
      });
      if (this.getUser.isAuth === true) {
        this.setNotice({
          state: true,
          title: " ",
          body: " ",
          button: "",
          style: {
            height: "100%",
            display: "inline-block"
          }
        });
      } else {
        this.setNotice({
          state: true,
          title: " ",
          body: "  : " + this.getUser.authState,
          button: "",
          style: {
            height: "100%",
            display: "inline-block"
          }
        });
      }
    },
    goLogin() {
      this.$router.push("login");
    }
  }
};
</script>

<style lang="scss">
@import "@/styles/gwahangmi/sign/signup.scss";
</style>
