<template>
  <div class="editor-wrap-wrap">
    <div
      class="detail-background"
      :style="computedHeight"
      v-on:click="closeEditor"
    ></div>

    <div class="editor-wrap" :style="computedDisplay">
      <div class="post-category-wrap">
        <div class="post-category">
          <select
            v-model="post.category"
            require
            name="postCategory"
            class="postCategory"
          >
            <option value="" disabled selected> </option>
            <option value="physics">:</option>
            <option value="chemistry">:</option>
            <option value="geoscience">:</option>
            <option value="biology">:</option>
          </select>
        </div>
      </div>

      <div class="post-title-input-wrap">
        <input class="post-title-input" type="text" v-model="post.title" />
      </div>
      <md-editor ref="mdEditor" v-model="post.content"></md-editor>

      <div class="post-upload-button-wrap">
        <div class="outter">
          <div class="inner">
            <button class="post-upload-button" v-on:click="postUpload">
              {{ post.category }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import MdEditor from "@/components/Posts/MdEditor.vue";

export default {
  name: "post-editor",
  components: {
    "md-editor": MdEditor
  },
  props: {
    height: String,
    display: String
  },
  data() {
    return {
      post: {
        category: "",
        title: "Hello, ",
        content: "<h1>Hello, </h1>"
      }
    };
  },
  computed: {
    ...mapGetters(["getUser"]),
    computedHeight() {
      return this.height;
    },
    computedDisplay() {
      return this.display;
    },
    getUID() {
      return this.getUser.uid;
    }
  },
  methods: {
    ...mapActions(["preUploadPost"]),
    ...mapActions(["setNotice"]),
    closeEditor() {
      this.$emit("close-editor");
    },
    postUpload() {
      if (this.post.category === "") {
        this.setNotice({
          state: true,
          title: " ",
          body: "    ",
          button: "",
          style: {
            height: "100%",
            display: "inline-block"
          }
        });
        return;
      }
      this.preUploadPost({
        category: this.post.category,
        title: this.post.title,
        content: this.post.content
      });
      this.setNotice({
        state: true,
        title: " !",
        body: this.post.category + "   ",
        button: "",
        style: {
          height: "100%",
          display: "inline-block"
        }
      });
      this.postDataInit();
      this.closeEditor();
    },
    postDataInit() {
      this.$refs.mdEditor.initValue();
      this.post.category = "";
      this.post.title = "Hello, ";
      this.post.content = "<h1>Hello, </h1>";
    }
  }
};
</script>

<style lang="scss">
@import "@/styles/gwahangmi/post/post-editor.scss";
</style>
